@media screen and (max-width: 480px) { footer .container .footer-logo span { text-align: center; } }

footer .container .footer-links { padding-left: 40px; }

@media screen and (max-width: 991px) { footer .container .footer-links { padding-left: 0; } }

@media screen and (max-width: 480px) { footer .container .footer-links { padding-left: 0; } }

footer .container .footer-links .f-nav { padding: 20px 0; display: flex; }

@media screen and (max-width: 991px) { footer .container .footer-links .f-nav { flex-wrap: wrap; justify-content: center; padding-bottom: 10px; padding-top: 10px; } }

footer .container .footer-links .f-nav__item { padding-right: 4px; padding-left: 7px; }

footer .container .footer-links .f-nav__item:first-child { padding-left: 0; }

footer .container .footer-links .f-nav__item__link { display: block; color: #252525; font-size: 14px; font-weight: 400; line-height: 20px; text-transform: uppercase; }

@media screen and (max-width: 480px) { footer .container .footer-links .f-nav__item__link { font-size: 14px; } }

footer .container .footer-links .f-nav__item__link:hover { color: #f6bd14; }

footer .container .footer-links .f-contacts { position: relative; color: #252525; display: flex; justify-content: space-between; }

@media screen and (max-width: 480px) { footer .container .footer-links .f-contacts { padding-bottom: 5px; } }

footer .container .footer-links .f-contacts__number { padding-left: 20px; display: block; position: relative; font-size: 22px; font-weight: 400; font-family: 'Roboto Condensed', sans-serif; text-align: right; color: #252525; }

@media screen and (max-width: 480px) { footer .container .footer-links .f-contacts__number { text-align: left; padding-left: 0; font-size: 18px; } }

footer .container .footer-links .f-contacts__number--bold { font-family: 'Roboto Bold Condensed', sans-serif; font-weight: 700; }

footer .container .footer-links .f-contacts__cr-link { text-align: right; text-decoration: underline; color: #2b2b2b; font-size: 14px; font-weight: 400; line-height: 31px; }

@media screen and (max-width: 480px) { footer .container .footer-links .f-contacts__cr-link { top: 0; width: 120px; font-size: 12px; line-height: 20px; } }

footer .container .footer-links .f-contacts__cr-link:hover { color: #f6bd14; }

.slick-prev, .slick-next { width: 70px; height: 70px; z-index: 3333; top: 40%; }

.slick-prev { left: 200px; }

@media screen and (max-width: 1199px) { .slick-prev { left: 70px; } }

@media screen and (max-width: 991px) { .slick-prev { right: 70px; } }

@media screen and (max-width: 480px) { .slick-prev { left: 20px; } }

.slick-prev:before { content: '>'; background-image: url("../img/rectangle-21-copy-2.png"); background-repeat: no-repeat; -webkit-background-size: 100%; background-size: 100%; color: transparent; width: 70px; height: 70px; }

.slick-next { right: 200px; }

@media screen and (max-width: 1199px) { .slick-next { right: 130px; } }

@media screen and (max-width: 991px) { .slick-next { right: 70px; } }

@media screen and (max-width: 480px) { .slick-next { right: 20px; } }

.slick-next:before { content: '<'; background-image: url("../img/rectangle-21.png"); background-repeat: no-repeat; -webkit-background-size: 100%; background-size: 100%; color: transparent; width: 70px; height: 70px; }

.slider div { height: 194px; background-size: cover; }

@media screen and (max-width: 1199px) { .slider div { height: 120px; } }

@media screen and (max-width: 991px) { .slider div { height: 80px; } }

@media screen and (max-width: 480px) { .slider div { height: 50px; } }

.slider div img { width: 100%; }

.slider .slide { position: relative; }

.slider .slide__mask { padding-top: 72px; position: absolute; width: 100%; height: 100%; background-color: #f6bd14; display: none; opacity: 0.7; font-size: 17px; font-family: 'Roboto Bold Condensed'; text-align: center; }

@media screen and (max-width: 1199px) { .slider .slide__mask { padding-top: 45px; } }

@media screen and (max-width: 991px) { .slider .slide__mask { padding-top: 27px; } }

@media screen and (max-width: 767px) { .slider .slide__mask { padding-top: 20px; } }

@media screen and (max-width: 480px) { .slider .slide__mask { font-size: 10px; } }

.slider .slide:hover { background-color: #f6bd14; }

.slider .slide:hover .slide__mask { display: block; }
